<template>
	<section class="column">
		<div class="loading"></div>
	</section>
</template>

<script>
var currColumn = null;
EventBus.sub('ChangePage', (target) => {
	if (!currColumn) return;
	currColumn.update();
});

export default {
	name: "Column",
	methods: {
		async update () {
			var loading = this.$el.querySelector('div.loading');
			loading.innerHTML = '';
			loading.classList.remove('hide');

			var category = null;
			if (this.$route.path === '/') {
				category = '/';
			}
			else if (this.$route.path === '/category') {
				if (!!this.$route.query && !!this.$route.query.c) {
					category = '/' + this.$route.query.c.split(',').filter(c => c.length > 0).join('/');
				}
				else {
					loading.innerHTML = '';
					return;
				}
			}
			else {
				loading.innerHTML = '';
				return;
			}
			console.log(category);



			setTimeout(() => {
				loading.classList.add('hide');
			}, 1000);
		}
	},
	mounted () {
		currColumn = this;
		this.update();
	},
	destroyed () {
		currColumn = null;
		var loading = this.$el.querySelector('div.loading');
		loading.classList.remove('hide');
	}
}
</script>